// Driver
import Driver from 'driver.js';
import 'driver.js/dist/driver.min.css';


function driver() {
    console.log('driverContent called')
    const driver = new Driver({
      opacity: 0.5,
      stageBackground: '#404746'
    });
    // Define the steps for introduction
    driver.defineSteps([
      {
        element: '.avatar-img',
        popover: {
          className: 'first-step-popover-class',
          title: 'Welcome',
          description: 'This is my img',
          position: 'bottom'
        }
      },
      {
        element: '.projectHead',
        popover: {
          title: 'Projects',
          description: 'Which could contain some details',
          position: 'top'
        }
      },

      {
        element: '.project-container h2',
        popover: {
          className: 'first-step-popover-class',
          title: 'Project name',
          // description: 'Nothing especial, move on to next one please~',
          position: 'bottom'
        }
      },
      {
        element: '.project-skill',
        popover: {
          title: 'The skills',
          description: '',
          position: 'bottom'
        }
      },

      {
        element: '.project-description',
        popover: {
          title: 'Details',
          description: 'What it is for',
          position: 'bottom'
        }
      },
      {
        element: '.project-container',
        popover: {
          title: "That's all",
          description: 'Thanks, have fun!',
          position: 'bottom'
        }
      },
    ]);
    // Start the introduction
    driver.start();
  };

export default driver;